/* @flow */

import { getUser } from './localStorage';
import history from './history';
import { setGraphTableData, fetchGraph } from './actions/graph';
import { logout } from './actions/user';
import { clearThemeState } from './actions/theme';
import { clearHomeState } from './actions/home';
import { clearUsermanagementState } from './actions/userManagement'
import { SESSION_EXPIRY_MESSAGE } from './constants';

type Fetch = (url: string, options: ?any) => Promise<any>;

type Options = {
  baseUrl: string,
};

function shouldAddContectType(method: string) {
  return ['POST', 'PUT', 'PATCH', 'post', 'put', 'patch'].indexOf(method) > -1;
}

/**
 * Creates a wrapper function around the HTML5 Fetch API that provides
 * default arguments to fetch(...) and is intended to reduce the amount
 * of boilerplate code in the application.
 * https://developer.mozilla.org/docs/Web/API/Fetch_API/Using_Fetch
 */
function createFetch(fetch: Fetch, { baseUrl }: Options) {
  // NOTE: Tweak the default options to suite your application needs
  const defaults = {
    method: 'GET',
    credentials: 'include',
    headers: {
      Accept: 'application/json',
    },
  };

  return async (url: string, options: any) => {
    // TODO: implement fetchInAction to reuse app state instead of localStorage
    const { token } = getUser() || {};
    const mergeOptions = {
      ...defaults,
      ...options,
      headers: {
        // Authorization: token || undefined,
        'X-Auth-Token': token || undefined,
        // Only add Content-Type for required methods
        ...(options &&
          options.method &&
          shouldAddContectType(options.method) && {
            'Content-Type': 'application/json',
          }),
        ...defaults.headers,
        ...(options && options.headers),
      },
    };
    const result = url.startsWith('/signIn')
      ? fetch(`${baseUrl}${url}`, options)
      : fetch(`${baseUrl}${url}`, mergeOptions);
    result.then(res => {
      if (res.status === 401) {
        logout();
        setGraphTableData({
          graphTableEdges: [],
          graphTableNodes: [],
        });
        fetchGraph({
          edges: [],
          vertices: [],
        });
        clearThemeState();
        clearHomeState();
        clearUsermanagementState();
        history.push('/login', { message: SESSION_EXPIRY_MESSAGE });
      }
    });
    return result;
  };
}

export default createFetch;
