/* eslint-disable import/no-extraneous-dependencies */
import { ACTIONS } from '../constants';

export function getAnalyticsData(data) {
  return {
    type: ACTIONS.GET_ANALYTICS_DATA,
    payload: data,
  };
}

export function clearHomeState() {
  return {
    type: ACTIONS.CLEAR_THEME_STATE,
    payload: null,
  };
}