/* eslint-disable import/prefer-default-export */

import { ACTIONS } from '../constants';

export function fetchGraph(graphData) {
  return {
    type: ACTIONS.FETCH_GRAPH,
    payload: graphData,
  };
}

export function changeDomain(label) {
  return {
    type: ACTIONS.CHANGE_DOMAIN,
    payload: label,
  };
}

export function queryUpdate(query) {
  return {
    type: ACTIONS.QUERY_UPDATE,
    payload: query,
  };
}

export function layoutUpdate(layout) {
  return {
    type: ACTIONS.LAYOUT_UPDATE,
    payload: layout,
  };
}

export function switchQueryBuilder(queryBuilder) {
  return {
    type: ACTIONS.SWITCH_QUERY_BUILDER,
    payload: queryBuilder,
  };
}

export function clearGraphState() {
  return {
    type: ACTIONS.CLEAR_GRAPH_STATE,
    payload: null,
  };
}

export function setGraphTableData(data) {
  return {
    type: ACTIONS.SET_GRAPH_TABLE_DATA,
    payload: data,
  };
}

export function setActiveVerticalTab(activeIndex) {
  return {
    type: ACTIONS.SET_ACTIVE_VERTICAL_TAB,
    payload: activeIndex,
  };
}

export function setRepositories(repositories) {
  return {
    type: ACTIONS.SET_REPOSITORIES,
    payload: repositories,
  };
}

export function setOrphanKeys(orphanKeys) {
  return {
    type: ACTIONS.SET_ORPHAN_KEYS,
    payload: orphanKeys,
  };
}