import { ACTIONS } from '../constants';

export default function graph(state = null, { type, payload }) {
  switch (type) {
    case ACTIONS.FETCH_GRAPH:
      return payload
        ? {
            ...state,
            ...payload,
          }
        : null;

    case ACTIONS.CHANGE_DOMAIN:
      return payload
        ? {
            ...state,
            ...payload,
          }
        : null;

    case ACTIONS.QUERY_UPDATE:
      return payload
        ? {
            ...state,
            ...payload,
          }
        : null;

    case ACTIONS.LAYOUT_UPDATE:
      return payload
        ? {
            ...state,
            ...payload,
          }
        : null;

    case ACTIONS.SWITCH_QUERY_BUILDER:
      return payload
        ? {
            ...state,
            ...payload,
          }
        : null;
    case ACTIONS.SET_GRAPH_TABLE_DATA:
      return payload
        ? {
            ...state,
            ...payload,
          }
        : null;
    case ACTIONS.SET_ACTIVE_VERTICAL_TAB:
      return payload
        ? {
            ...state,
            ...payload,
          }
        : null;
    case ACTIONS.SET_REPOSITORIES:
      return payload
        ? {
            ...state,
            ...payload,
          }
        : null;
    case ACTIONS.SET_ORPHAN_KEYS:
      return payload
        ? {
            ...state,
            ...payload,
          }
        : null;

    case ACTIONS.CLEAR_GRAPH_STATE:
      return null;
    default:
      return state;
  }
}
