/* eslint-disable no-param-reassign */
import UniversalRouter from 'universal-router';
import { get, includes } from 'lodash';
import routes from './routes';

export default new UniversalRouter(routes, {
  resolveRoute(context, params) {
    const { user } = context.store.getState();
    if (process.env.BROWSER && context.route.protected && !user) {
      return { redirect: '/login', continue: context.pathname };
    }

    if (
      !includes(get(user, 'role'), 'Super Admin') &&
      !includes(get(user, 'role'), 'Admin') &&
      includes(['/user-management'], context.pathname)
    ) {
      return { redirect: '/unauthorized', continue: context.pathname };
    }
    if (typeof context.route.load === 'function') {
      return context.route
        .load()
        .then(action => action.default(context, params));
    }
    if (typeof context.route.action === 'function') {
      return context.route.action(context, params);
    }
    return undefined;
  },
});
