import { ACTIONS } from '../constants';

export default function user(state = null, { type, payload }) {
  switch (type) {
    case ACTIONS.INIT_USER:
      return payload
        ? {
            ...state,
            ...payload,
          }
        : null;
    case ACTIONS.LOGIN:
      return {
        ...state,
        ...payload,
      };
    case ACTIONS.LOGOUT:
      return null;
    case ACTIONS.GET_USER_INFO:
      return {
        ...state,
        ...payload,
      };
    default:
      return state;
  }
}
