/* eslint-disable import/no-extraneous-dependencies */
import { ACTIONS } from '../constants';

export function getGroups(groups) {
  return {
    type: ACTIONS.GET_GROUPS,
    payload: groups,
  };
}

export function getUsers(users) {
  return {
    type: ACTIONS.GET_USERS,
    payload: users,
  };
}

export function changeTab(tab) {
  return {
    type: ACTIONS.CHANGE_UM_TAB,
    payload: tab,
  };
}

export function getPermissions(permissions) {
  return {
    type: ACTIONS.GET_USER_PERMISSIONS,
    payload: permissions,
  };
}

export function setUser(selectedUser) {
  return {
    type: ACTIONS.SET_USER,
    payload: selectedUser,
  };
}

export function setUsers(selectedUsers) {
  return {
    type: ACTIONS.SET_USERS,
    payload: selectedUsers,
  };
}

export function setUserFieldError(error) {
  return {
    type: ACTIONS.SET_USER_FIELDS_ERROR,
    payload: error,
  };
}

export function clearUsermanagementState() {
  return {
    type: ACTIONS.CLEAR_USERMANAGEMENT,
    payload: null,
  };
}
