/* eslint-disable global-require */
// The top-level (parent) route
const routes = {
  path: '',

  // Keep in mind, routes are evaluated in order
  children: [
    {
      path: '',
      load: () => import(/* webpackChunkName: 'login' */ './login'),
    },
    {
      path: '/login',
      load: () => import(/* webpackChunkName: 'login' */ './login'),
    },
    {
      path: '/home',
      protected: true,
      load: () => import(/* webpackChunkName: 'home' */ './home'),
    },
    {
      path: '/groups-list',
      load: () => import(/* webpackChunkName: 'groups-list' */ './groups-list'),
      protected: true,
    },
    {
      path: '/groups-create',
      load: () =>
        import(/* webpackChunkName: 'groups-create' */ './groups-create'),
      protected: true,
    },
    {
      path: '/groups-update/:id',
      load: () =>
        import(/* webpackChunkName: 'groups-update' */ './groups-update'),
      protected: true,
    },
    {
      path: '/user-management',
      load: () =>
        import(/* webpackChunkName: 'user-management' */ './user-management'),
      protected: true,
    },
    {
      path: '/users-list',
      load: () => import(/* webpackChunkName: 'users-list' */ './users-list'),
      protected: true,
    },
    {
      path: '/users-create',
      load: () =>
        import(/* webpackChunkName: 'users-create' */ './users-create'),
      protected: true,
    },
    {
      path: '/users-update/:id',
      load: () =>
        import(/* webpackChunkName: 'users-update' */ './users-update'),
      protected: true,
    },
    {
      path: '/my-profile/:id',
      load: () => import(/* webpackChunkName: 'my-profile' */ './my-profile'),
      protected: true,
    },
    {
      path: '/reset-password/:id',
      load: () =>
        import(/* webpackChunkName: 'reset-password' */ './reset-password'),
      protected: true,
    },
    {
      path: '/graph-visual',
      load: () =>
        import(/* webpackChunkName: 'graph-visual' */ './graph-visual'),
      protected: true,
    },
    {
      path: '/unauthorized',
      load: () =>
        import(/* webpackChunkName: 'unauthorized' */ './unauthorized'),
      protected: true,
    },
    // Wildcard routes, e.g. { path: '(.*)', ... } (must go last)
    {
      path: '(.*)',
      load: () => import(/* webpackChunkName: 'not-found' */ './not-found'),
    },
  ],

  async action({ next }) {
    // Execute each child route until one of them return the result
    const route = await next();

    // Provide default values for title, description etc.
    route.title = `${route.title || 'Untitled Page'}`;
    route.description = route.description || '';

    return route;
  },
};

// The error page is available by permanent url for development mode
if (__DEV__) {
  routes.children.unshift({
    path: '/error',
    action: require('./error').default,
  });
}

export default routes;
