import { ACTIONS } from '../constants';

export default function userManagement(state = null, { type, payload }) {
  switch (type) {
    case ACTIONS.GET_GROUPS:
      return payload
        ? {
            ...state,
            ...payload,
          }
        : null;

    case ACTIONS.GET_USERS:
      return payload
        ? {
            ...state,
            ...payload,
          }
        : null;

    case ACTIONS.CHANGE_UM_TAB:
      return payload
        ? {
            ...state,
            ...payload,
          }
        : null;

    case ACTIONS.GET_USER_PERMISSIONS:
      return payload
        ? {
            ...state,
            ...payload,
          }
        : null;

    case ACTIONS.SET_USER:
      return payload
        ? {
            ...state,
            ...payload,
          }
        : null;

    case ACTIONS.SET_USERS:
      return payload
        ? {
            ...state,
            ...payload,
          }
        : null;

    case ACTIONS.SET_USER_FIELDS_ERROR:
      return payload
        ? {
            ...state,
            ...payload,
          }
        : null;

    case ACTIONS.CLEAR_THEME_STATE:
      return null;
    default:
      return state;
  }
}
