import { ACTIONS } from '../constants';

export default function theme(state = null, { type, payload }) {
  switch (type) {
    case ACTIONS.SELECT_DOMAIN:
      return payload
        ? {
            ...state,
            ...payload,
          }
        : null;

    case ACTIONS.SELECT_NODE:
      return payload
        ? {
            ...state,
            ...payload,
          }
        : null;

    case ACTIONS.SELECT_CURRENT_NODE:
      return payload
        ? {
            ...state,
            ...payload,
          }
        : null;

    case ACTIONS.SELECT_RELATION:
      return payload
        ? {
            ...state,
            ...payload,
          }
        : null;

    case ACTIONS.SELECT_THEME:
      return payload
        ? {
            ...state,
            ...payload,
          }
        : null;

    case ACTIONS.SELECT_NODE_SIZE:
      return payload
        ? {
            ...state,
            ...payload,
          }
        : null;

    case ACTIONS.SELECT_EDGE_SIZE:
      return payload
        ? {
            ...state,
            ...payload,
          }
        : null;

    case ACTIONS.CHECK_DOMAIN_OR_TYPE:
      return payload
        ? {
            ...state,
            ...payload,
          }
        : null;
    case ACTIONS.SWITCH_NODE_THEME_PREFERENCES:
      return payload
        ? {
            ...state,
            ...payload,
          }
        : null;

    case ACTIONS.CLEAR_THEME_STATE:
      return null;
    default:
      return state;
  }
}
