import { ACTIONS } from '../constants';

export function selectDomain(domain) {
  return {
    type: ACTIONS.SELECT_DOMAIN,
    payload: domain,
  };
}

export function selectNode(node) {
  return {
    type: ACTIONS.SELECT_NODE,
    payload: node,
  };
}

export function selectCurrentNode(currentNode) {
  return {
    type: ACTIONS.SELECT_CURRENT_NODE,
    payload: currentNode,
  };
}

export function selectTheme(domainTheme) {
  return {
    type: ACTIONS.SELECT_THEME,
    payload: domainTheme,
  };
}

export function selectRelation(relation) {
  return {
    type: ACTIONS.SELECT_RELATION,
    payload: relation,
  };
}

export function selectNodeSize(nodeSize) {
  return {
    type: ACTIONS.SELECT_NODE_SIZE,
    payload: nodeSize,
  };
}

export function selectEdgeSize(edgeSize) {
  return {
    type: ACTIONS.SELECT_EDGE_SIZE,
    payload: edgeSize,
  };
}

export function clearThemeState() {
  return {
    type: ACTIONS.CLEAR_THEME_STATE,
    payload: null,
  };
}

export function checkDomainOrType(domainOrType) {
  return {
    type: ACTIONS.CHECK_DOMAIN_OR_TYPE,
    payload: domainOrType,
  };
}

export function switchNodeThemePreferences(preference) {
  return {
    type: ACTIONS.SWITCH_NODE_THEME_PREFERENCES,
    payload: preference,
  };
}