import { ACTIONS } from '../constants';

export default function home(state = null, { type, payload }) {
  switch (type) {
    case ACTIONS.GET_ANALYTICS_DATA:
      return payload
        ? {
            ...state,
            ...payload,
          }
        : null;
    case ACTIONS.CLEAR_USERMANAGEMENT:
      return null;
    default:
      return state;
  }
}