import { ACTIONS } from '../constants';

export default function notifier(state = null, { type, payload }) {
  switch (type) {
    case ACTIONS.OPEN_NOTIFIER:
      return payload
        ? {
          ...state,
          ...payload,
        }
        : null;

    case ACTIONS.CLOSE_NOTIFIER:
      return payload
        ? {
          ...state,
          ...payload,
        }
        : null;
    case ACTIONS.CLEAR_NOTIFIER_STATE:
      return null;
    default:
      return state;
  }
}
